.todo-container {
  height: 800px;
}

@media (max-height: 720px) {
  .todo-container {
    height: 650px;
  }
}
@media (max-height: 600px) {
  .todo-container {
    height: 580px;
  }
}
@media (max-width: 399px) {
  .todo-container {
    width: 350px;
  }
}
@media (min-width: 400px) {
  .todo-container {
    width: 400px;
  }
}

.todo-title {
  position: absolute;
  display: flex;
  flex-direction: column;
  transition: 1s;
  left: 50%;
  top: 20%;
  transform: translate(-50%);
  padding-bottom: 25px;
  border-bottom: 1px solid transparent;
  width: 100%;
}

.todo-title-move {
  top: 0;
  border-bottom: 1px solid rgb(209 213 219);
}

.todo-tabs {
  position: absolute;
  top: 15%;
}
.todo-tabs .active {
  border-bottom: 2px solid blue;
}
.todo-tabs button {
  padding: 0 20px;
  border-bottom: 1px solid rgb(209 213 219);
  font-size: 22px;
}

.todo-list {
  top: 20%;
  width: 100%;
  max-height: 550px;
  overflow-y: auto;
}
.todo-list li {
  border-bottom: 2px solid rgb(209 213 219);
  width: 100%;
  padding: 10px 20px;
  font-size: 22px;
}
.form-holder {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: 1s;
}
.form-holder button {
  top: 50%;
  transform: translateY(-50%);
  left: 17px;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.6s;
  color: white;
}
.form-holder input {
  width: 200px;
  transition: 0.6s;
  border: 1px solid rgb(209 213 219);
  outline: none;
  cursor: pointer;
}
.form-holder input::placeholder {
  color: #fff;
  font-size: 24px;
  position: absolute;
  transition: 0.6s;
}
.show-input {
  width: 365px !important ;
  background-color: transparent !important;
  cursor: text !important;
}
.show-input::placeholder {
  color: rgb(107 114 128) !important;
  rotate: 360deg;
}
.show-input-btn {
  left: 85% !important ;
  color: black !important;
}
.form-holder-move {
  top: 95% !important;
}
.form-holder-move input {
  width: 60px;
  height: 60px;
}
.form-holder-move button {
  left: 20px;
}

.welcome-msg {
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  transition: 1s;
  z-index: 1;
}
.welcome-msg-move {
  padding-bottom: 0 !important;
  opacity: 0;
  z-index: -1;
}

.holder {
  z-index: -1;
  background-color: transparent;
  width: 100vw;
  height: 100vw;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
@media (min-width: 640px) {
  .holder {
    width: 500px;
    height: 850px;
    border: 2px solid rgb(107 114 128);
    border-radius: 22px;
    box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1),
      0 4px 6px -4px rgb(0 0 0 / 0.1);
  }
}
.popup-shadow {
  box-shadow: 0 30px 100px -3px rgb(0 0 0 / 0.3),
    0 4px 6px -4px rgb(0 0 0 / 0.1);
}
